//fonts
$google-font: 'https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Nunito:wght@600;700&display=swap';

@font-face {
  font-family: 'icomoon';
  src: url('../fonts/icomoon.eot?vpxnzh');
  src: url('../fonts/icomoon.eot?vpxnzh#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?vpxnzh') format('truetype'),
    url('../fonts/icomoon.woff?vpxnzh') format('woff'),
    url('../fonts/icomoon.svg?vpxnzh#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^='icon-'],
[class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  //   speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-star:before {
  content: '\e900';
  color: #f4c300;
}
.icon-facebook:before {
  content: '\ea90';
}
.icon-instagram:before {
  content: '\ea92';
}
.icon-twitter:before {
  content: '\ea96';
}

$baseFontSize: 16px;
$baseFontFamily: 'DM Sans', sans-serif;
$regularFontWeight: 400;
$mediumFontWeight: 500;
$semiBoldFontWeight: 600;
$boldFontWeight: 700;

$mobilePadding: (12px / $baseFontSize) * 1rem;
$desktopPadding: 1rem;

// Paths
$i: '../i/';
$fontsPath: '../fonts/';
$usersFilesPath: '../userfiles/';

