.frame {
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include e(title) {
    @include font('Nunito', 24px, $boldFontWeight);
  }
  @include e(login-block) {
    text-align: right;
  }
}

.menu {
  list-style: none;
  display: none;
  padding: 0;
  line-height: 21px;
  justify-content: space-between;
  text-align: center;
  @include e(link) {
    @include font('DM Sans', 16px, $regularFontWeight);
    text-decoration: none;
  }
  @include e(item) {
    padding: 10px 20px;
  }
}
.login-block {
  @include e(btn) {
    border: none;
    line-height: 21px;
    @include font('DM Sans', 16px, $regularFontWeight);
    @include m(login) {
      background: none;
    }
    @include m(signUp) {
      background: $green;
      color: $white;
      font-size: 14px;
      padding: 12px 16px;
      border-radius: 4px;
    }
  }
}
