.page {
  @include e(img) {
    width: 100%;
    @include m(about) {
      width: 95%;
      margin-top: 30px;
      margin-left: 30px;
      margin-bottom: -30px;
      margin-right: -30px;
    }
    @include m(subscribe) {
      width: auto;
      max-height: 360px;
    }
  }
  @include e(img-box) {
    display: block;
    @include m(about) {
      background: $green;
      width: 100%;
      max-width: 440px;
    }
    @include m(subscribe) {
      display: none;
    }
    @include m(review) {
      display: none;
    }
  }
  @include e(main-block) {
    display: flex;
    justify-content: space-between;
  }
  @include e(about) {
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
  }
}
.block {
  display: block;
  max-width: 50%;
  @include e(analitics) {
    max-width: 80%;
    margin-top: 30px;
  }
  @include m(collection) {
    display: contents;
  }
  @include m(about) {
    margin-left: 30px;
  }
  @include m(products) {
    display: contents;
  }
  @include m(review) {
    max-width: 100%;
  }
  @include m(subscribe) {
    max-width: 100%;
  }
  @include m(instagram) {
    max-width: 100%;
  }
  @include e(button) {
    @include m(login) {
      margin-right: 20px;
    }
  }
}
