.frame {
  @include e(title) {
    margin: 5px;
  }
  @include e(elements) {
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  @include m(footer) {
    flex-direction: column;
    text-align: center;
  }
}
.frame-list {
  list-style: none;
  padding: 0;
  display: none;
  @include e(item) {
    @include font('DM Sans', 16px, $regularFontWeight, $navItemsText);
    margin-top: 16px;
  }
}

.social {
  display: flex;
  max-width: 180px;
  justify-content: space-between;
  margin: auto;
  @include e(item) {
    text-decoration: none;
    background: $socialItemsBg;
    height: 48px;
    width: 48px;
    position: relative;
    margin-top: 5px;
    &::before {
      content: '';
      position: absolute;
      height: 24px;
      background-position: center center;
      background-size: contain;
      background-repeat: no-repeat;
    }
    @include m(f) {
      &::before {
        background-image: url(../i/facebook-icon.svg);
        width: 13px;
        top: 10px;
        left: 16px;
      }
    }
    @include m(i) {
      &::before {
        background-image: url(../i/instagram-icon.svg);
        width: 24px;
        top: 12px;
        left: 12px;
      }
    }
    @include m(t) {
      &::before {
        background-image: url(../i/twitter-icon.svg);
        width: 30px;
        top: 13px;
        left: 11px;
      }
    }
  }
}
