.block {
  @include e(title) {
    @include m(main) {
      @include font('Nunito', 52px, $boldFontWeight);
      line-height: 72px;
      margin-bottom: 20px;
    }
  }
  @include e(analitics) {
    max-width: 80%;
    margin-top: 30px;
  }
  @include m(subscribe) {
    max-width: 60%;
    text-align: left;
  }
}
.page {
  @include e(img-box) {
    top: -5px;
    @include m(subscribe) {
      display: block;
      width: 80%;
      max-height: 360px;
      text-align: right;
      position: absolute;
      right: 0;
    }
  }
  @include e(subscribe) {
    display: flex;
    align-items: center;
    padding-right: 10px;
    position: relative;
  }
}
.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
