.gallery {
  display: flex;
  justify-content: center;
  // flex-wrap: wrap;
  @include e(item) {
    margin-left: 10px;
    &:first-of-type {
      margin-left: 0;
    }
    @include m(1) {
      background: $lightRose;
    }
    @include m(2) {
      background: $lightPurple;
    }
    @include m(3) {
      background: $lightGreen;
    }
  }
  @include e(img) {
    max-width: 100%;
  }
  @include e(i) {
    max-width: 230px;
  }
}
.block {
  z-index: 1;
  display: contents;
  @include m(collection) {
    & .block__link {
      grid-row: 3;
      justify-self: center;
      margin-top: 30px;
      @include font('DM Sans', 18px, $regularFontWeight, $descriptionText);
      text-decoration: none;
      position: relative;
      &::after {
        content: '';
        position: absolute;
        background-image: url(../i/arrow.svg);
        width: 18px;
        height: 15px;
        background-position: center center;
        background-size: contain;
        top: 5px;
        margin-left: 10px;
      }
    }
  }
  @include e(text) {
    display: contents;
  }
}
