.products {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-evenly;
  margin-top: 30px;
  margin-bottom: 40px;
  @include e(item) {
    min-width: 270px;
    text-align: center;
    margin: 20px 10px 0 0;
    padding: 0 0 20px 0;

    @include m(1) {
      background: $lightGreen;
    }
    @include m(2) {
      background: $ligthCoffee;
    }
    @include m(3) {
      background: $lightDarkGrey;
    }
    @include m(4) {
      background: $lightBlue;
    }
  }
}

.tabs {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
  margin: 8px 0 0 0;
  @include e(item) {
    margin-right: 24px;
    &:last-of-type {
      margin-right: 0;
    }
    @include font('DM Sans', 18px, $mediumFontWeight, $descriptionText);
    @include m(active) {
      color: $green;
    }
  }
}
.product {
  display: flex;
  flex-direction: column;
  padding: 5px 0 30px 0;
  @include e(title) {
    margin: 10px 0 10px 0;
  }
  @include e(stars) {
    margin: 0 0 10px 0;
  }
  @include e(price) {
    @include font('Nunito', 16px, $semiBoldFontWeight);
    margin: 0;
  }
  @include e(img-box) {
    order: -1;
  }
}
