.form {
  display: block;
  position: relative;
  max-width: 425px;
  width: 100%;
  @include e(input) {
    position: relative;
    vertical-align: middle;
    max-width: 415px;
    width: 100%;
  }
}
.block {
  @include e(button) {
    @include m(subscribe) {
      position: absolute;
      width: 125px;
      padding: 10px 0;
      right: -17px;
      top: 50%;
      margin-top: -30px;
    }
  }
}
