.page {
  @include e(img) {
    width: 100%;
  }
  @include e(main-block) {
    display: grid;
  }
  @include e(img-box) {
    grid-row: 2 / 4;
    @include m(review) {
      display: none;
    }
    @include m(subscribe) {
      display: none;
      z-index: -1;
    }
  }
  @include e(about) {
    display: flex;
    flex-direction: column;
    margin: 50px auto 0 auto;
    max-width: 1100px;
  }
  @include e(collection) {
    display: grid;
    margin: 30px auto 0 auto;
    max-width: 1150px;
    grid-template-columns: 1fr;
  }
  @include e(products) {
    display: flex;
    flex-direction: column;
    margin: 70px auto 0 auto;
    justify-content: center;
    align-items: center;
  }
  @include e(review) {
    display: flex;
    flex-direction: column;
    margin-top: 70px;
    margin: 70px auto 0 auto;
    justify-content: center;
    align-items: center;
  }
  @include e(subscribe) {
    background: $pistachio;
    padding: 10px 55px 40px 55px;
    max-width: 1040px;
    margin: 70px auto 0 auto;
    text-align: center;
  }
  @include e(instagram) {
    margin: 70px auto 0 auto;
  }
  @include e(footer) {
    margin: 70px auto 70px auto;
  }
}

.block {
  display: contents;
  @include e(title) {
    @include font('Nunito', 32px, $boldFontWeight);
    line-height: 32px;
    grid-column: 1 / 3;
    @include m(products) {
      text-align: center;
    }
  }
  @include e(description) {
    @include font('DM Sans', 16px, $regularFontWeight, $descriptionText);
    line-height: 25.6px;
    margin-bottom: 30px;
  }
  @include e(button) {
    border-radius: 4px;
    border: none;
    background: $green;
    padding: 16px 32px;
    @include font('DM Sans', 18px, $mediumFontWeight, $white);
    grid-row: 3;
    cursor: pointer;
    max-width: 160px;
    max-height: 55px;
    @include m(login) {
      background: none;
      @include font('DM Sans', 16px, $regularFontWeight);
    }
    @include m(signUp) {
      background: $green;
      color: $white;
      font-size: 14px;
      padding: 12px 16px;
      border-radius: 4px;
    }
    @include m(about) {
      margin-top: 30px;
      order: 1;
    }
    @include m(products) {
      display: block;
      max-width: 160px;
    }
  }
  @include e(analitics) {
    display: flex;
    justify-content: space-around;
    margin-top: 30px;
    grid-row: 4;
    grid-column: 1 / 3;
  }
  @include m(instagram) {
    text-align: center;
  }
  @include m(frame) {
    display: block;
  }
}
.analitics {
  text-align: center;
  @include e(num) {
    @include font('Nunito', 26px, $boldFontWeight, $analitics);
    margin: 0;
  }
  @include e(text) {
    @include font('DM Sans', 18px, $regularFontWeight);
  }
}
