.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include e(input) {
    border: none;
    border-radius: 4px;
    padding: 18px;
    margin-bottom: 20px;
    width: 90%;
    &::placeholder {
      @include font('DM Sans', 14px, $regularFontWeight, $navItemsText);
    }
  }
}
