.block {
  @include e(gallery) {
    grid-row: 1;
    grid-column: 2;
  }
  @include e(text) {
    display: block;
    max-width: 390px;
  }
  @include e(main-title) {
    margin-bottom: 24px;
  }
  @include e(description) {
    margin-bottom: 40px;
  }
  @include e(analitics) {
    margin-top: 60px;
  }
}
.gallery {
  justify-content: right;
  @include e(item) {
    padding: 15px;
    margin-right: 10px;
    &:first-of-type {
      margin-left: 0;
    }
  }
  @include e(i) {
    cursor: pointer;
    position: relative;

    &:hover:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 5px;
      background-color: rgba(0, 0, 0, 0.3);
      background-image: url(../i/instagram.svg);
      background-size: 80px 80px;
      background-repeat: no-repeat;
      background-position: center;
    }
  }
  @include m(instagram) {
    justify-content: center;
  }
}
