.analitics {
  text-align: center;
  @include e(num) {
    @include font('Nunito', 36px, $boldFontWeight, $analitics);
    margin: 0;
  }
  @include e(text) {
    @include font('DM Sans', 18px, $regularFontWeight);
  }
}
.block {
  @include e(analitics) {
    justify-content: space-between;
  }
  @include m(review) {
    width: 80%;
  }
  @include e(description) {
    @include m(review) {
      max-width: 90%;
    }
    @include m(footer) {
      margin-bottom: 10px;
    }
  }
  @include e(button) {
    &:hover {
      background: $hoverBtn;
    }
    &:active {
      background: $activeBtn;
    }
    @include m(login) {
      &:hover {
        background: none;
      }
      &:active {
        background: none;
      }
    }
  }
}
.page {
  @include e(img-box) {
    top: -320px;
    z-index: 0;
    margin-right: 30px;
    text-align: right;
    @include m(review) {
      display: block;
      margin-left: -30px;
      position: relative;
      top: 45px;
    }
    @include m(subscribe) {
      top: -50px;
    }
  }
  @include e(img) {
    max-width: 80%;
    @include m(about) {
      max-width: 100%;
    }
    @include m(review) {
      max-width: 100%;
    }
  }
  @include e(collection) {
    margin-top: 100px;
  }
  @include e(about) {
    margin-top: 130px;
  }
  @include e(products) {
    margin-top: 130px;
  }
  @include e(review) {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  @include e(subscribe) {
    margin-top: 130px;
  }
  @include e(instagram) {
    margin-top: 90px;
  }
}
.menu {
  display: flex;
}
