.comment {
  background: $green;
  padding: 20px 30px 30px 30px;
  position: relative;
  @include e(text) {
    @include font('DM Sans', 16px, $regularFontWeight, $descriptionLigthText);
    &::after {
      content: '';
      position: absolute;
      background: url(../i/quote.svg);
      width: 40px;
      height: 40px;
      background-size: contain;
      background-position: center;
      top: 10%;
      right: 5%;
    }
  }
}
.profile {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-column-gap: 10px;
  @include e(img-box) {
    max-width: 60px;
    max-height: 60px;
    grid-column: 1;
    grid-row: 1 / 3;
  }
  @include e(img) {
    width: 100%;
  }
  @include e(name) {
    @include font('Nunito', 18px, $semiBoldFontWeight, $white);
    margin: 0;
  }
  @include e(profession) {
    @include font('DM Sans', 14px, $regularFontWeight, $profText);
  }
}
